// import { useEffect } from "react";
import uniqid from "uniqid";

const ClientForm = ({ fields }: any) => {
    console.log(fields);
  const inputs = fields.map((e: any) => {
    const inputType = () => {
      if (e.fieldType === "text") {
        return <input type="text" key={uniqid()}/>;
      } else if (e.fieldType === "number") {
        return <input type="number" key={uniqid()}/>;
      } else if (e.fieldType === "select") {
        return (
          <select key={uniqid()}>
            {e.fieldOptions.map((e : any) => {
              <option value={e.value}>{e.value}</option>;
            })}
          </select>
        );
      }
    };
    return (
      <div>
        <p>{e.fieldName}</p>
        {inputType()}
      </div>
    );
  });

  return <div>{inputs}</div>;
};

export default ClientForm;
